@charset "UTF-8";
/*Обнуление*/
@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@700&family=Roboto:wght@300;400&display=swap");
* {
  padding: 0;
  margin: 0;
  border: 0; }

*, *:before, *:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box; }

:focus, :active {
  outline: none; }

a:focus, a:active {
  outline: none; }

nav, footer, header, aside {
  display: block; }

html, body {
  height: 100%;
  width: 100%;
  font-size: 100%;
  line-height: 1;
  font-size: 14px;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%; }

input, button, textarea {
  font-family: inherit; }

input::-ms-clear {
  display: none; }

button {
  cursor: pointer; }

button::-moz-focus-inner {
  padding: 0;
  border: 0; }

a, a:visited {
  text-decoration: none; }

a:hover {
  text-decoration: none; }

ul li {
  list-style: none; }

img {
  vertical-align: top; }

h1, h2, h3, h4, h5, h6 {
  font-size: inherit;
  font-weight: inherit; }

/*--------------------*/
a {
  color: #000;
  text-decoration: none; }

a.text:active,
a.text:hover,
a.text {
  text-decoration: none;
  color: #666; }

input[type="search"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button,
input[type="search"]::-webkit-search-results-decoration {
  -webkit-appearance: none; }

* {
  margin: 0;
  padding: 0; }

html {
  font-size: 1rem;
  color: #000; }

body {
  font-family: 'Roboto', sans-serif;
  overflow-wrap: break-word;
  word-wrap: break-word; }

.content {
  display: flex;
  flex-direction: column;
  flex: 1 0 auto; }

.container {
  max-width: 100%;
  min-width: 320px; }

.wrap {
  min-width: 320px;
  max-width: 1366px;
  padding: 0 1rem;
  margin: 0 auto;
  box-sizing: border-box; }

.form__wrap {
  min-width: 320px;
  max-width: 410px;
  padding: 0 1rem;
  margin: 0 auto;
  box-sizing: border-box; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0, 0, 0, 0);
  overflow: hidden; }

.header {
  display: flex;
  margin: 0 auto;
  justify-content: center;
  align-content: center; }

.header__list {
  display: flex;
  justify-content: center;
  align-content: center;
  text-align: center; }

.form__wrap {
  box-shadow: white; }

.profile__head {
  font-family: "Montserrat", sans-serif;
  font-weight: 700;
  font-size: 2rem;
  line-height: 40px;
  text-align: left;
  margin-bottom: 21px; }

.registration__tabs {
  display: flex;
  flex-direction: row;
  color: #6b6b6b;
  line-height: 24px; }
  .registration__tabs .tab {
    font-weight: 400; }
  .registration__tabs .tab:last-child {
    margin-left: 2rem; }

.tab-active {
  color: #000;
  padding-bottom: 15px;
  border-bottom: 2px solid #6e41e2; }

.social {
  background: #fafafa;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  padding-top: 20px;
  padding-bottom: 20px; }
  .social p {
    font-family: "Roboto";
    font-size: 0.8rem;
    font-style: normal;
    font-weight: 400;
    line-height: 20px;
    text-align: left; }
  .social .social__icons {
    display: flex;
    flex-direction: row; }
    .social .social__icons .social__icons-img {
      max-width: 36px;
      max-height: 36px; }

.inputs {
  display: flex;
  flex-direction: column; }
  .inputs input {
    min-height: 56px;
    width: 100%;
    box-shadow: 0px 4px 16px 0px rgba(51, 51, 51, 0.08); }

.form__checkbox {
  font-size: 1rem;
  line-height: 24px;
  font-weight: 400px; }
  .form__checkbox a {
    color: blue; }

.btn__form {
  font-weight: 400;
  font-size: 1rem;
  line-height: 24px;
  padding-top: 10px;
  padding-bottom: 10px;
  width: 100%;
  background: #ffffff;
  color: #6e41e2;
  text-align: center;
  border-radius: 4px;
  margin-bottom: 8px; }

.btn__form-active {
  color: #ffffff;
  background: #6e41e2; }

.wrap {
  min-width: 320px;
  max-width: 1366px;
  padding: 0 1rem;
  margin: 0 auto;
  box-sizing: border-box; }

.nav {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: baseline;
  max-height: 76px;
  margin-top: 20px;
  margin-bottom: 40px; }

.nav__logo {
  font-family: 'Montserrat', sans-serif;
  font-size: 2rem;
  text-align: left;
  font-weight: 700; }

.nav__menu {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: baseline; }

.menu__item {
  margin-right: 24px;
  font-size: 1rem;
  line-height: 24px;
  color: rgba(17, 17, 17, 0.48);
  border-bottom: 2px solid #FFF; }
  .menu__item:hover {
    border-bottom: 2px solid #6E41E2;
    color: #000;
    transition: transform 1s; }

.nav__user {
  display: flex;
  align-items: baseline;
  color: #6E41E2; }
  .nav__user p {
    margin-right: 12px; }

.nav__avatar {
  padding: 10px;
  background: #F1F1F1;
  text-align: center;
  align-content: center;
  justify-content: center;
  border-radius: 50%; }

.header {
  display: flex;
  flex-direction: column; }

.header__title {
  font-family: 'Montserrat', sans-serif;
  font-weight: 700;
  line-height: 48px;
  font-size: 2.5rem;
  text-align: left;
  display: block; }

.header__text {
  font-weight: 400;
  font-style: 1rem;
  line-height: 24px;
  display: block; }

.search {
  display: grid;
  grid-template-columns: 3fr 1fr;
  grid-gap: 20px; }
  .search input {
    padding-left: 25px;
    width: 96%;
    height: 56px;
    box-shadow: 0px 4px 16px 0px rgba(51, 51, 51, 0.08);
    -webkit-appearance: none;
    -moz-appearance: none;
    -ms-appearance: none;
    appearance: none;
    background: url("../../img/cross.svg") no-repeat right center; }

.search__line {
  position: relative;
  font-size: 1rem;
  font-weight: 400;
  line-height: 24px;
  color: #8d8d8d;
  width: 100%; }

.search__line::before {
  position: absolute;
  content: url("../../img/search.svg");
  top: 17px;
  box-shadow: 0px 4px 16px 0px rgba(51, 51, 51, 0.08); }

select {
  height: 56px;
  background: #0000;
  width: 100%;
  box-shadow: 0px 4px 16px 0px rgba(51, 51, 51, 0.08);
  font-weight: 400;
  font-size: 1rem;
  line-height: 24px;
  -webkit-appearance: none;
  -moz-appearance: none;
  -ms-appearance: none;
  appearance: none;
  background: url("../../img/arrov-down.svg") no-repeat right center; }

.select__countries {
  position: relative; }

.select__countries::before {
  position: absolute;
  content: "Your country";
  color: #8d8d8d;
  font-size: 0.8rem;
  line-height: 20px;
  font-weight: 400; }

.select__countries::after {
  position: absolute;
  content: url("../../img/cross.svg");
  right: 20px;
  top: 22px; }

.cards {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 10px;
  margin-top: 32px; }

.card {
  position: relative;
  max-width: 400px;
  display: flex;
  flex-direction: column;
  border-radius: 8px;
  overflow: hidden; }

.card__image {
  max-height: 200px; }
  .card__image img {
    max-width: 100%;
    height: auto;
    min-height: 136px;
    object-fit: cover; }

.card__avatar {
  display: flex;
  flex-direction: row;
  align-items: center;
  position: absolute;
  top: 85px;
  left: 20px;
  color: #ffffff;
  font-weight: 400;
  font-size: 1rem;
  line-height: 24px; }
  .card__avatar .card__avatar-img {
    max-width: 56px;
    max-height: 56px; }
  .card__avatar .cards__name {
    margin-left: 12px; }

.card__progressbar {
  position: absolute;
  background: #27ae60;
  width: 46px;
  height: 24px; }

.card__progressbar-text {
  position: absolute;
  width: 100%;
  padding-left: 20px;
  padding-top: 2px;
  color: #ffff;
  font-size: 0.8rem;
  text-align: left; }

.card__title {
  margin-top: 24px;
  margin-left: 20px;
  margin-bottom: 13px;
  font-family: "Montserrat", sans-serif;
  font-weight: 700;
  line-height: 28px;
  font-size: 1.25rem;
  text-align: left; }

.lessons__resource {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  width: 100%;
  justify-content: space-evenly; }
  .lessons__resource .lessons__resource-item {
    margin: 15px;
    flex-wrap: nowrap; }
    .lessons__resource .lessons__resource-item img {
      max-width: 20px;
      max-height: 14px; }

.btn__row {
  display: flex;
  flex-wrap: wrap; }

.card__btn {
  display: block;
  background: #ffff;
  color: #6e41e2;
  font-size: 1rem;
  line-height: 24px;
  text-align: center;
  padding: 30px 40px;
  border: 1px solid #ffffff; }
  .card__btn:hover {
    cursor: pointer;
    border: 1px solid #6e41e2;
    border-radius: 4px;
    transition: all 0.7s; }

@media (max-width: 725px) {
  .cards {
    grid-template-columns: 1fr;
    grid-template-rows: 1fr 1fr 1fr;
    align-self: center; }
  .card {
    margin: 0 auto; }
  .btn__row {
    flex-direction: column;
    width: 100%; } }

